@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(12,0,222);
  background: linear-gradient(90deg, rgba(12,0,222,1) 0%, rgba(12,176,234,1) 16%, rgba(231,14,164,1) 79%, rgba(0,212,255,1) 100%);
  font-family: 'Staatliches', cursive;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.imgg {
    font-size: 50px;
    letter-spacing: 3px;
    transition: all 1s;
}

.imgg {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}

.custom-border {
    border-color: black;
}


.custom-input {
    border: 2px solid red;
    border-radius: 300px;
}

.custom-input:hover {
    background-color: black;
    color: white;
}


ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

li {
    display: inline-block;
    margin: 10px;
    padding: 10px;
    font-size: 30px;
    border: 5px solid  linear-gradient(90deg, rgba(12,0,222,1) 0%, rgba(12,176,234,1) 16%, rgba(231,14,164,1) 79%, rgba(0,212,255,1) 100%);
    background-color: blue;
    border-radius: 300px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

